footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;


}

footer a {
    color:var(--color-bg)
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display:flex;
    flex-wrap: wrap;
    justify-content:center;
    gap: 2rem; 
    margin: 0 auto 3rem;
}


li{
    list-style: none;
}


.footer__socials{
    display:flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background:var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border:1px solid transparent;

}


.footer__socials a :hover{
    background:transparent;
    color: var(--color-bg);

    border-color: var(--color-bg);
}

.footer__copyright{
    margin-bottom: 6rem;
    color: var(--color-bg);
    font-size:1.3rem;
}


/* ========================media queries for MEDIUM Devices======================= */

@media screen and (max-width:1024px){

    .experience__container{
        grid-template-columns:1fr;
    }
    .experience__container > div{
        width: 80%;
         padding: 2rem;  
         margin: 0 auto;
     }   
.experience__content{
    padding: 1rem;
}    

    }
    
    /* ========================media queries for SMALL Devices======================= */
    
    
    @media screen and (max-width:600px){
        .permalinks{
            flex-direction: column;
            gap:1.5rem;
        }
        .footer__socials{
            margin-bottom: 2.6rem;
          }
    }
